export function delay(ms = 1000) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

/** short of stopEventPropagation */
export function ste(e) {
  // 
  if (e) {
    if (e.preventDefault) {
      e.preventDefault();
    }
    if (e.stopPropagation) {
      e.stopPropagation();
    }
  }
  return true;
};
