/* eslint-disable no-unused-vars,semi,react/prop-types */
/** @typedef {import('types/domain').SubApp} SubApp */
/** @typedef {import('types/domain').SubAppVersion} SubAppVersion */
import React from 'react';
import { Row, Col, Tag, message, Button, Input, Divider, Spin, Radio, Alert, Tooltip } from 'antd';
import {
  CloseCircleOutlined, CheckCircleOutlined, ExclamationCircleOutlined, SmileOutlined,
  CopyOutlined, ArrowRightOutlined, BugOutlined, WindowsOutlined,
} from '@ant-design/icons';
import copy from 'copy-to-clipboard';
import { useC2Mod, typeCtxM, getModelState } from 'services/concent';
import GeneralTable from 'components/GeneralTable';
import { NormalBlank, NormalSpanBlank, VerticalBlank } from 'components/dumb/Blank';
import { VERSION_LIST } from 'configs/constant/ccModule';
import { getAppPipelineProjSpace } from 'services/common';
import * as dateUtil from 'utils/date';
import * as helper from './logic/helper';

const stTitle = {
  color: 'rgba(0,0,0,.7)',
  verticalAlign: 'top',
};
const stCol = { minHeight: '26px' };

const listModeOptions = [
  { label: '', value: 'all' },
  { label: '', value: 'tag' },
];

const Label = (props) => <span style={{ color: '#838383de' }}>{props.children}</span>;

const Title = React.memo(() => {
  const { state, syncer, sync, mr } = useC2Mod(VERSION_LIST, { tag: 'VTableTitle' });
  const placeholder = state.changeMode === 'gray' ? '' : '';
  const inputTitle = state.changeMode === 'gray'
    ? <><BugOutlined /><NormalBlank /><span style={{ color: 'grey' }}>: </span></>
    : <><WindowsOutlined /><NormalBlank /><span style={{ color: 'blue' }}>:  </span></>;
  const uiGrayEnabled = state.subApp.enable_gray === 1
    ? <span><Label></Label> <CheckCircleOutlined style={{ color: 'green' }} /><Divider type="vertical" /></span>
    : <span><Label></Label> <CloseCircleOutlined style={{ color: 'red' }} /><Divider type="vertical" /></span>;

  let uiGrayStatus = '';
  let uiGrayUsers = '';
  if (state.subApp.enable_gray === 1) {
    uiGrayStatus = <span>
      <Label>{state.subApp.build_version}</Label> <Divider type="vertical" />
       {state.subApp.is_in_gray
        ? <><Label></Label> <ExclamationCircleOutlined style={{ color: 'red' }} /></>
        : <><Label></Label> <SmileOutlined style={{ color: 'green' }} /></>
      }
    </span>
    uiGrayUsers = <div style={{ wordBreak: 'break-all', wordWrap: 'break-word' }}>
      <Label>{state.subApp.gray_users.join(',')}</Label>
    </div>
  }

  let uiChangeGrayVerBtn = '';
  if (state.subApp.enable_gray === 1) {
    uiChangeGrayVerBtn = <>
      <Button onClick={mr.showChangeGrayInput} danger></Button>
      <NormalBlank />
    </>;
  }

  let uiAgreeGray = '';
  // 
  if (state.subApp.is_in_gray) {
    uiAgreeGray = <>
      <Button loading={state.grayBtnLoading} type="primary" onClick={mr.clickAgreeGrayBtn}></Button>
      <NormalBlank />
    </>
  }

  const { user } = getModelState('portal').userInfo;
  return <div>
    <div>{user}
      Hel  <Tag color={helper.tagColors.online}>{state.buildTotal}</Tag>
       <Tag color={helper.tagColors.globalMarked}>{state.markTotal}</Tag>
    </div>
    <div><Label>{state.subApp.online_version}</Label></div>
    <div>{uiGrayEnabled}{uiGrayStatus}</div>
    {uiGrayUsers}
    <div style={{ float: 'right', margin: '12px 12px 0 0' }}>
      <Radio.Group options={listModeOptions} onChange={e => mr.changeListMode(e.target.value)}
        value={state.listMode} style={{ width: '150px' }}
      />
      <NormalBlank />
      {uiAgreeGray}
      {uiChangeGrayVerBtn}
      <Tooltip title="">
        <Button onClick={mr.showChangeOnlineInput} danger></Button>
      </Tooltip>
      <NormalBlank />
      <Button onClick={mr.freshSubAppAssociateAndTable}></Button>
    </div>
    {state.showInput
      ? <div style={{ float: 'right', marginRight: '12px', width: '100%' }}>
        <VerticalBlank height="12px" />
        <div style={{ float: 'right' }}>
          {inputTitle}
          <Input style={{ width: '360px' }} value={state.inputVersion} onChange={syncer.inputVersion} placeholder={placeholder} />
          <NormalBlank />
          <Button loading={state.updateLoading} type="primary" onClick={mr.clickUpdateVersionBtn}></Button>
          <NormalBlank />
          <Button onClick={sync('showInput', false)}></Button>
        </div>
      </div>
      : ''
    }
  </div>
});

const renderVersion = (record, tableCtx, titleUiMode = 'hasTitle') => {
  const [cardLoading, setCardLoading] = React.useState(false);
  const { hasTopPagination, total, uiPagination } = tableCtx;
  const { $uiType } = record;
  if ($uiType === 'topPagination') { // 
    return hasTopPagination && total > 5 && uiPagination;
  }
  if ($uiType === 'switchAllTip' || $uiType === 'switchMarkTip') {
    if (total === 0) {
      return helper.buildHelModCreationTutorialTip(titleUiMode);
    }
    return $uiType === 'switchAllTip'
      ? helper.buildSwitchAllTip(titleUiMode)
      : helper.buildSwitchMarkTip(titleUiMode);
  }

  // 
  const {
    uiConner, stCard, CardComp, uiGlobalMarkDesc, uiUserMarkDesc,
  } = helper.buildConnerAndStCard(record, { stTitle, stCol });
  const actions = helper.buildCardActions(record, setCardLoading, titleUiMode);
  const { pureGitRepoUrl, uiGitHashLinks } = helper.buildUiGitHashLinks(record);
  const verStr = record.version_tag || record.sub_app_version;

  const copyVersion = (e) => {
    helper.ste(e);
    copy(verStr);
    message.success(` ${verStr} `, 1);
  };
  const copyGitBranch = (e) => {
    helper.ste(e);
    copy(record.git_branch);
    message.success(` ${record.git_branch} `, 1);
  };

  return <Spin spinning={cardLoading}>
    <CardComp actions={actions} style={stCard} hoverable={true}>
      <Row gutter={5} style={{ position: 'relative' }}>
        <Col span="13" style={stCol}><span style={stTitle}></span>{verStr} <CopyOutlined onClick={copyVersion} /></Col>
        <Col span="11" style={stCol}><span style={stTitle}></span>{dateUtil.tryGetLocaleStrOfISOStr(record.create_at)}</Col>
        <Col span="13" style={stCol}>
          <span style={stTitle}></span>
          {record.create_by}
        </Col>
        <Col span="11" style={{ ...stCol, verticalAlign: 'top' }}>
          <span style={stTitle}></span> {record.git_branch} <NormalSpanBlank />
          <CopyOutlined onClick={copyGitBranch} /><NormalSpanBlank />
          <a target="_blank" rel="noopener noreferrer" href={`${pureGitRepoUrl}/tree/${record.git_branch}`} onClick={helper.ste}>
            <ArrowRightOutlined />
          </a>
        </Col>
        <Col span="13" style={stCol}><span style={stTitle}>git  hash</span>{uiGitHashLinks}</Col>
        <Col span="11" style={stCol}>
          <span style={stTitle}></span>{record.project_name}<NormalSpanBlank />
          <a target="_blank" rel="noopener noreferrer" href={getAppPipelineProjSpace(record)} onClick={helper.ste}>
            <ArrowRightOutlined />
          </a>
        </Col>
        <Col span="24" style={stCol}><span style={stTitle}>git</span>{record.desc}</Col>
        {uiGlobalMarkDesc}
        {uiUserMarkDesc}
        {uiConner}
      </Row>
    </CardComp>
  </Spin>;
};


/** @type {()=>import('antd/lib/table/interface').ColumnsType<SubAppVersion>} */
const getColumns = (tableCtx) => [
  {
    key: 'op',
    title: () => <Title />,
    render: (val, record) => renderVersion(record, tableCtx),
  },
];

const titleTip = "id";
const getNoTitleColumns = (tableCtx) => [
  {
    key: 'op',
    title: () => <Alert type="info" message={titleTip} />,
    render: (val, record) => renderVersion(record, tableCtx, 'noTitle'),
  },
];


function setup(c) {
  const ctx = typeCtxM(VERSION_LIST, {}, c);
  const ins = ctx.initState({ pFetchSubAppEnd: false });

  ctx.mr.freshSubAppAssociate(ctx.props.name).then(() => {
    ins.setState({ pFetchSubAppEnd: true });
  });

  return {
    ins,
    fetchFn: ctx.mr.fetchVersionList,
    pageSizeOptions: ['10', '20', '30'],
  };
}

function AppVersionList(props) {
  const { titleUiMode = 'hasTitle' } = props;
  const { settings } = useC2Mod(VERSION_LIST, { setup, props, tag: 'AppVersionList' });
  const getColumnsFn = titleUiMode === 'hasTitle' ? getColumns : getNoTitleColumns;
  const { state } = settings.ins;

  return (
    <>
      {state.pFetchSubAppEnd && (
        <GeneralTable tid="vTable" fetchFn={settings.fetchFn} getColumns={getColumnsFn}
          pageSizeOptions={settings.pageSizeOptions} hasTopPagination={true}
        />
      )}
    </>
  );
}

AppVersionList.displayName = 'AppVersionList';

export default React.memo(AppVersionList);
