import React from 'react';
import { Tooltip, Tag } from 'antd';
import { QuestionCircleOutlined } from '@ant-design/icons';
import { appNameTip, appTokenTip, appGroupNameTip, isLocalRender, appRenderHostTip } from 'configs/constant/formTip';
import { APP_NAME_RULE } from 'configs/constant/share';
import { NormalBlank } from 'components/dumb/Blank';
import TokenViewer from './components/TokenViewer';
import {
  fieldAppEnablePipeline, fieldAppCnName, fieldAppDesc, fieldAppEnableGray, fieldAppOwners, fieldAppGrayUsers,
  fieldIsTest, defaultValues, fieldClassId,
} from '../SubAppCreateForm/fieldConf';
import * as helUtil from 'utils/hel';

const commonRules = [
  { pattern: APP_NAME_RULE, message: '' },
  { max: 64, type: 'string', message: '30' },
];

const stTooltip = { zIndex: 9999999992 };

const fields = [
  {
    type: 'input',
    options: {
      name: 'token',
      label: <TokenViewer>
        <span>token
          <Tooltip style={stTooltip} title={appTokenTip} color="blue" getTooltipContainer={helUtil.getAppBodyContainer}>
            <QuestionCircleOutlined />
          </Tooltip>
        </span>
        <NormalBlank />
      </TokenViewer>,
      disabled: true,
      placeholder: 'input app token',
      rules: [
        { required: true, message: 'token' },
      ],
    },
  },
  {
    type: 'input',
    options: {
      name: 'app_group_name',
      disabled: true,
      label: <span>
         <Tooltip style={stTooltip} title={appGroupNameTip} color="blue" getTooltipContainer={helUtil.getAppBodyContainer}><QuestionCircleOutlined /></Tooltip>
        <NormalBlank /><Tag color="blue">()</Tag>
      </span>,
      placeholder: 'input app group name',
      rules: [
        { required: true, message: '' },
        ...commonRules,
      ],
    },
  },
  {
    type: 'input',
    options: {
      name: 'name',
      disabled: true,
      label: <span>
         <Tooltip style={stTooltip} title={appNameTip} color="blue" getTooltipContainer={helUtil.getAppBodyContainer}><QuestionCircleOutlined /></Tooltip>
      </span>,
      placeholder: 'input app name',
      rules: [
        { required: true, message: '' },
        ...commonRules,
      ],
    },
  },
  fieldIsTest,
  fieldAppCnName,
  fieldAppDesc,
  fieldClassId,
  fieldAppOwners,
  fieldAppEnableGray,
  fieldAppGrayUsers,
  fieldAppEnablePipeline,
  {
    type: 'radioGroup',
    options: {
      name: 'is_local_render',
      initValue: defaultValues.is_local_render,
      options: [{ value: 1, label: '' }, { value: 0, label: '' }],
      label: <span> <Tooltip style={stTooltip} title={isLocalRender} color="blue" getTooltipContainer={helUtil.getAppBodyContainer}><QuestionCircleOutlined /></Tooltip></span>,
    },
  },
  {
    type: 'input',
    options: {
      name: 'render_app_host',
      label: <span>
         <Tooltip style={stTooltip} title={appRenderHostTip} color="blue" getTooltipContainer={helUtil.getAppBodyContainer}><QuestionCircleOutlined /></Tooltip>
        <NormalBlank /><Tag color="blue">{appRenderHostTip}</Tag>
      </span>,
    },
  },
  {
    type: 'input',
    options: {
      name: 'git_repo_url',
      label: <span>
        git <NormalBlank />
        <Tag color="blue">helpack</Tag>
      </span>,
      placeholder: 'input git repo url',
    },
  },
];

export const disabledFields = fields.map(item => {
  const itemCopy = { type: item.type, options: { ...item.options, disabled: true } };
  return itemCopy;
});

export default fields;
