export const remoteLibLazy = `
import helMicro from 'hel-micro';

//  helMicro.preFetchLib 
export async function callRemoteMethod(){
  const remoteLib = await helMicro.preFetchLib('remote-lib-tpl');
  return remoteLib.num.random(19);
}

/** --------------------------------------------------------------------------- */
// 
import type { Lib } from remote-lib-tpl';
import helMicro from 'hel-micro';

export async function  callRemoteMethod(){
  const remoteLib = await helMicro.preFetchLib<Lib>('remote-lib-tpl');
  return remoteLib.num.random(19);
}
`;


