/* eslint-disable no-unused-vars,semi,react/prop-types */
/** @typedef {import('types/domain').SubApp} SubApp */
/** @typedef {import('types/domain').IServerModStat} IServerModStat */
import React from 'react';
import { Row, Card, Col, Tag, message, Button, Radio } from 'antd';
import { CopyOutlined } from '@ant-design/icons';
import copy from 'copy-to-clipboard';
import { useC2Mod, typeCtxM, getModelState } from 'services/concent';
import GeneralTable from 'components/GeneralTable';
import { NormalBlank } from 'components/dumb/Blank';
import { SERVER_MOD_LIST } from 'configs/constant/ccModule';
import * as dateUtil from 'utils/date';
import * as helper from './logic/helper';
import styles from './styles.module.css';

const stTitle = {
  color: 'rgba(0,0,0,.7)',
  verticalAlign: 'top',
};
const stCol = { minHeight: '26px' };

const listModeOptions = [
  { label: '', value: 'card' },
  { label: '', value: 'table' },
];

const MyCol = (props) => {
  const { span = "12", colStyle = stCol, titleStyle = stTitle, title, label } = props;
  const copyLabel = (e) => {
    helper.ste(e);
    copy(label);
    message.success(`${title} ${label} `, 1);
  };

  return (
    <Col span={span} style={colStyle}>
      <span style={titleStyle}>{title}</span>
      {label} <CopyOutlined onClick={copyLabel} />
    </Col>
  )
};

const Head = React.memo(() => {
  const { state, mr } = useC2Mod(SERVER_MOD_LIST, { tag: 'SMHead' });
  const { user } = getModelState('portal').userInfo;

  return <div>
    <div>
      {user}
       <Tag color={helper.tagColors.online}>{state.total}</Tag>Node
    </div>
    <div style={{ float: 'right', margin: '12px 12px 0 0' }}>
      
      <Radio.Group options={listModeOptions} onChange={e => mr.changeListMode(e.target.value)}
        value={state.listMode} style={{ width: '150px' }}
      />
      <NormalBlank />
      <Button onClick={mr.refreshTableCurPage}></Button>
    </div>
  </div>
});

const renderCard = (/** @type {IServerModStat} */ record) => {
  const loadAt = dateUtil.tryGetLocaleStrOfISOStr(record.load_at);
  return <Card className={styles.srvModCardWrap} hoverable={true}>
    <div className={styles.srvModJsLogo} />
    <Row gutter={5} style={{ position: 'relative' }}>
      <MyCol title="" label={record.mod_version} />
      <MyCol title="" label={loadAt} />
      <MyCol title="" label={record.env_name} />
      <MyCol title="" label={record.container_name} />
      <MyCol title="ip" label={record.container_ip} />
      <MyCol title="pod" label={record.pod_name} />
      <MyCol title="" label={record.img_version} />
      <MyCol title="" label={record.city} />
    </Row>
  </Card>;
};

/** @type {()=>import('antd/lib/table/interface').ColumnsType<IServerModStat>} */
const getCardColumns = () => [
  {
    key: 'op',
    title: () => <span />,
    render: (val, record) => renderCard(record),
  },
];

const getColumns = () => [
  { dataIndex: 'mod_version', title: '' },
  { dataIndex: 'load_at', title: '', render: val => dateUtil.tryGetLocaleStrOfISOStr(val) },
  { dataIndex: 'env_name', title: '' },
  { dataIndex: 'container_name', title: '' },
  { dataIndex: 'container_ip', title: 'ip' },
  { dataIndex: 'pod_name', title: 'pod' },
  { dataIndex: 'img_version', title: '' },
  { dataIndex: 'city', title: '' },
];

function setup(c) {
  const ctx = typeCtxM(SERVER_MOD_LIST, {}, c);
  ctx.setState({ subAppName: ctx.props.name });

  return {
    fetchFn: ctx.mr.fetchDataList,
    pageSizeOptions: ['10', '20', '30'],
  };
}

function ServerModList(props) {
  const { settings, state } = useC2Mod(SERVER_MOD_LIST, { setup, props, tag: 'ServerModList' });
  const isCard = state.listMode === 'card';
  const getColumnsFn = isCard ? getCardColumns : getColumns;

  return (
    <>
      <Head />
      <GeneralTable tid="smTable" fetchFn={settings.fetchFn} getColumns={getColumnsFn}
        pageSizeOptions={settings.pageSizeOptions} hasTopPagination={true} noTableHead={isCard}
      />
    </>
  );
}

ServerModList.displayName = 'ServerModList';

export default React.memo(ServerModList);
