/** @typedef {import('types/domain').User} User*/
/** @typedef {import('hel-types').ISubApp} ISubApp*/
/** @typedef {import('hel-types').ISubAppVersion} ISubAppVersion*/
import * as objUtil from 'utils/object';
import * as cst from 'configs/constant';
import { getUrlAppName } from 'services/common';

export function getInitialState() {
  let toMerge = {};
  try {
    const persistedState = JSON.parse(localStorage.getItem(cst.LS_HUB_PORTAL_STATE));
    const {
      userInfo, starAppNames, name2SubApp,
      visitAppNames, justLoadByHrefRefresh, persistTime, basicDataReady,
    } = persistedState;

    const timeDiff = Date.now() - persistTime;
    if (timeDiff > 3000) {
      // url3storagestore
      toMerge = { persistTime };
    } else {
      // helcommonServ.historyPushcommonServ.loadAppWithForceRefresh
      // portalStatepersistTime
      // refreshheltimeDiff3
      toMerge = {
        userInfo, starAppNames, name2SubApp, visitAppNames, justLoadByHrefRefresh,
        persistTime, basicDataReady,
      };
    }
  } catch (err) {
    // pass
  }

  let activeApp = getUrlAppName();
  // __hubhelbasename
  if (activeApp.startsWith('__hub')) {
    activeApp = '';
  }

  // 
  const sideBarVisible = !activeApp;

  let initialState = {
    delayRemoveStyle: 0,
    /** loadingrenderFullPage */
    loading: false,
    /**  */
    sideBarVisible,
    /**  */
    ballVisible: !sideBarVisible,
    /**  */
    contentVisible: sideBarVisible,
    basicDataReady: false,
    /** @type {User} -  */
    userInfo: { user: '', icon: '' },
    /** @type {string[]} -  */
    starAppNames: [],
    /** @type {string[]} -  */
    visitAppNames: [],
    /** @type {{ name: string, ver: string, desc: string, time: number }[]} -  */
    userMarkedList: [],
    activeApp, // 
    /** @type {Record<string, ISubAppVersion>} id  map */
    appVerId2detail: {},
    /** @type {Record<string, ISubApp>} - map */
    name2SubApp: {},
    persistTime: 0,
    moduleLoadtime: Date.now(),
    /** location.href */
    justLoadByHrefRefresh: false,
  };

  initialState = objUtil.safeAssign(initialState, toMerge);
  return initialState;
}

export default getInitialState;
