import React from 'react';
import { Row, Spin, Empty, Button } from 'antd';
import { AppstoreOutlined } from '@ant-design/icons';
import SubApp from 'components/SubApp';
import * as commonService from 'services/common';
import { useC2Mod, typeCtxM } from 'services/concent';
import { LATEST_VISIT } from 'configs/constant/ccModule';
import { STORE } from 'configs/constant/page';

function setup(c) {
  const ctx = typeCtxM(LATEST_VISIT, {}, c);
  ctx.effect(() => {
    ctx.mr.initVisitList();
  }, []);
  return {
    gotoStorePage: () => commonService.historyPush(STORE),
  };
}

export default React.memo(() => {
  const { state, settings } = useC2Mod(LATEST_VISIT, { setup });
  return (
    <Spin spinning={state.loading}>
      {state.subApps.length === 0 && !state.loading
        ? <Empty
          image="https://gw.alipayobjects.com/zos/antfincdn/ZHrcdLPrvN/empty.svg"
          imageStyle={{
            height: 80,
          }}
          description={<span>  </span>}
        >
          <Button type="primary" icon={<AppstoreOutlined />} onClick={settings.gotoStorePage}></Button>
        </Empty> : ''
      }
      <Row gutter={[16, 16]}>
        {state.subApps.map(v => <SubApp key={v.name} delVisit={true} appData={v} module={LATEST_VISIT} />)}
      </Row>
    </Spin>
  );
});
