
export default {
  //  get 
  get url() {
    return /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-.,@?^=%&:/~+#]*[\w\-@?^=%&/~+#])?/;
  },
  /**   */
  get letterOrNum() {
    return /^[A-Za-z0-9|_|-]+$/;
  },
  /**   */
  get letterNumOrCn() {
    return /^[\u4e00-\u9fa5A-Za-z0-9-|_]+$/;
  },
};
